import React from 'react';
import {useEffect } from "react";
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Slider from '@material-ui/core/Slider';
import Input from '@material-ui/core/Input';

const useStyles = makeStyles({
    root: {
        width: 150,
        height: 250,
    },
});

const marks = [
    {
        value: 0,
        label: '0%',
    },
    {
        value: 25,
        label: '25%',
    },
    {
        value: 50,
        label: '50%',
    },
    {
        value: 75,
        label: '75%',
    },
    {
        value: 100,
        label: '100%',
    },
];


export default function InputSlider(props) {
    const classes = useStyles();
    const [value, setValue] = React.useState(props.value.value);
    const name = React.useState(props.name);
    const socket = props.socket;

    useEffect(() =>{
        socket.on('accountein',(fan,temp,hum,id) =>{
            if(id===props.id){
                props.onEin(fan,temp,hum);
            }
        });
    })

    const onChangeCommitted = (event , newValue) => {
        socket.emit("slider",newValue,props.id);
    }

    const handleSliderChange = (event, newValue) => {
        if(props.value.onoff){
            setValue(newValue);

            if(newValue === 0) {
                props.onZero(props.id);
            }
            else{
                if (!(props.onoff)){
                    props.notZero(props.id);
                }
            }
        }
        else {
            setValue(0);
        }
    };

    const handleInputChange = (event) => {
        if(props.value.onoff){
            if((event.target.value === '' ? '' : Number(event.target.value)) === 0) {
                props.onZero(props.id);
                setValue(0);
            }
            else{
                if (!(props.onoff)){
                    props.notZero(props.id);
                    setValue(event.target.value === '' ? '' : Number(event.target.value));
                    socket.emit("slider",event.target.value === '' ? '' : Number(event.target.value),props.id);
                }
            }
        }
        else {
            setValue(0);
        }
    };

    const handleBlur = () => {
        if (value < 0) {
            setValue(0);
        } else if (value > 100) {
            setValue(100);
        }
    };

    return (
        <div className={classes.root}>
            <Typography id="input-slider" gutterBottom>
                {name}
            </Typography>
            <Grid container spacing={0} >
                <Grid item xs>
                    <div className={classes.root}>
                        <Slider
                            orientation="vertical"
                            value={typeof value === 'number' ? value : 0}
                            onChange={handleSliderChange}
                            onChangeCommitted={onChangeCommitted}
                            marks={marks}
                            aria-labelledby="input-slider"
                        />
                    </div>
                </Grid>
                <Grid item>
                    <Input
                        value={value}
                        onChange={handleInputChange}
                        onBlur={handleBlur}
                        inputProps={{
                            step: 5,
                            min: 0,
                            max: 100,
                            type: 'number',
                            'aria-labelledby': 'input-slider',
                        }}
                    />
                </Grid>
            </Grid>
        </div>
    );
}
