import React, {Component} from 'react';
import '../App.css';
import InputSlider from "./slider";
import Camera from "./camera";
import {io} from "socket.io-client";
import Sensor from "./sensor";
import Creat from "./creat";
import Distance from "./distance";


let re=new RegExp('(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)(\\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)){3}|https?:\\/\\/(www\\.)?[-a-zA-Z0-9@:%._\\+~#=]{1,256}\\.[a-zA-Z0-9()]{1,6}\\b|https?:\\/\\/localhost')
let ip= window.location.href;
let halter= re.exec(ip);
let URL= halter[0]+":5000";
let socket;
let multex= true;
const halt=1;
let time;
let time2;


class Body extends Component{
    state ={
        sliders:[
            {id: 1, value: {value:50,onoff:true}, name:"L端fter"},
            {id: 2, value: {value:100,onoff:true}, name:"Heizspule"},
            {id: 3, value: {value:0,onoff:true}, name:"Befeuchter"},
        ],
        sensor:[
            {id:1},
            {id:2},
            {id:3},
            {id:4},
        ],
        slider: true,
        camera: false,
        creat: false,
        socket:io(URL,  { transports: ["websocket"] })
    }
     Zero=(id)=>{
        if(id===1)
        {
            //Slider 3
            let items = [...this.state.sliders];
            // 2. Make a shallow copy of the item you want to mutate
            let item = {...items[2]};
            // 3. Replace the property you're intested in
            item.value.onoff = false;
            // 4. Put it back into our array. N.B. we *are* mutating the array here, but that's why we made a copy first
            items[2] = item;
            // 5. Set the state to our new copy
            this.setState({items});
            // Slider 2
            let itemis = [...this.state.sliders];
            // 2. Make a shallow copy of the item you want to mutate
            let itemi = {...itemis[1]};
            // 3. Replace the property you're intested in
            itemi.value.onoff = false;
            // 4. Put it back into our array. N.B. we *are* mutating the array here, but that's why we made a copy first
            itemis[1] = itemi;
            // 5. Set the state to our new copy
            this.setState({itemis});
        }
    }
    notZero=(id)=> {
        if (id === 1) {
            //Slider 3
            let items = [...this.state.sliders];
            // 2. Make a shallow copy of the item you want to mutate
            let item = {...items[2]};
            // 3. Replace the property you're intested in
            item.value.onoff = true;
            // 4. Put it back into our array. N.B. we *are* mutating the array here, but that's why we made a copy first
            items[2] = item;
            // 5. Set the state to our new copy
            this.setState({items});
            // Slider 2
            let itemis = [...this.state.sliders];
            // 2. Make a shallow copy of the item you want to mutate
            let itemi = {...itemis[1]};
            // 3. Replace the property you're intested in
            itemi.value.onoff = true;
            // 4. Put it back into our array. N.B. we *are* mutating the array here, but that's why we made a copy first
            itemis[1] = itemi;
            // 5. Set the state to our new copy
            this.setState({itemis});
        }
    }

    Ein=(fan,temp,hum)=>{
        let items = [...this.state.sliders];
        // 2. Make a shallow copy of the item you want to mutate
        let item = {...items[0]};
        // 3. Replace the property you're intested in
        item.value.value = fan;
        // 4. Put it back into our array. N.B. we *are* mutating the array here, but that's why we made a copy first
        items[0] = item;
        // 5. Set the state to our new copy
        this.setState({items});
        let itemis = [...this.state.sliders];
        // 2. Make a shallow copy of the item you want to mutate
        let itemi = {...itemis[1]};
        // 3. Replace the property you're intested in
        itemi.value.value = temp;
        // 4. Put it back into our array. N.B. we *are* mutating the array here, but that's why we made a copy first
        itemis[1] = itemi;
        // 5. Set the state to our new copy
        this.setState({itemis});
        let itemes = [...this.state.sliders];
        // 2. Make a shallow copy of the item you want to mutate
        let iteme = {...itemes[2]};
        // 3. Replace the property you're intested in
        iteme.value.value = hum;
        // 4. Put it back into our array. N.B. we *are* mutating the array here, but that's why we made a copy first
        items[2] = iteme;
        // 5. Set the state to our new copy
        this.setState({itemes});
    }
    Ready=()=>{
        multex=true;
    }

    Creat=()=>{
        this.setState({creat:true});
    }

    notCreat=()=>{
        this.setState({creat:false});
    }

    shutDown=()=>{
        socket=this.state.socket;
        socket.emit("shutdown")
    }

    changePage=()=> {
        if(this.state.slider){
            socket=this.state.socket;
            this.setState({slider:false});
            this.setState({camera:true});
            time= setInterval(function(){if(multex){socket.emit("cameratrig",halt);multex=false;}}, 50);
            console.log(URL);
            clearInterval(time2);
        }
        else{
            this.setState({slider:true});
            this.setState({camera:false});
            clearInterval(time);
            time2=setInterval(function(){socket.emit("sensortrig");} , 1000);
        }
    }

    render() {
        return (
            <div>
                 <table>
                    <th>
                        <button id="page" onClick={this.changePage}>Switch Page</button>
                        {this.state.sensor.map(sensor => <div><Sensor id={sensor.id} key={sensor.id} socket={this.state.socket} sensor={0}/></div>)}
                    </th>
                    <tr>
                        <td>
                            <div>
                                {this.state.slider && this.state.sliders.map(slider => <div className="App"><InputSlider key={slider.id} value={slider.value} onZero={this.Zero} notZero={this.notZero} id={slider.id} name={slider.name} socket={this.state.socket} onEin={this.Ein}  /></div>)}
                                {this.state.camera && <Camera socket={this.state.socket} onReady={this.Ready} onCreat={this.Creat}/>}
                                {this.state.creat&&<Creat socket={this.state.socket} notCreat={this.notCreat}/>}
                                {<Distance socket={this.state.socket}/>}
                                <button id="shutdown" onClick={this.shutDown}>Sleep</button>
                            </div>
                        </td>
                    </tr>
                </table>
            </div>
        );
    }
}
export default  Body;