import React from 'react';
import TextField from '@material-ui/core/TextField';
import { makeStyles } from '@material-ui/core/styles';
import '../App.css';


const useStyles = makeStyles((theme) => ({
    root: {
        '& .MuiTextField-root': {
            margin: theme.spacing(1),
            width: '25ch',
        },
    },
}));

export default function Creat(props) {
    const socket = props.socket;
    const classes = useStyles();
    const [name, setName] = React.useState('Adi');
    const [fan, setFan] = React.useState('30');
    const [temp, setTemp] = React.useState('40');
    const [hum, setHum] = React.useState('100');
    const handleChange = (event) => {
        setName(event.target.value);
    };
    const handleChange0 = (event) => {
        setFan(event.target.value);
    };
    const handleChange1 = (event) => {
        setTemp(event.target.value);
    };
    const handleChange2 = (event) => {
        setHum(event.target.value);
    };
    const sendData=()=>{
        socket.emit("account",(name,fan,temp,hum));
        props.notCreat();
    }

    return (
        <form className={classes.root} noValidate autoComplete="off">
            <div>
                <TextField
                    id="filled-name"
                    label="Name"
                    value={name}
                    onChange={handleChange}
                    variant="outlined"
                />
                <TextField
                    id="filled-name"
                    label="L端fter"
                    value={fan}
                    onChange={handleChange0}
                    variant="outlined"
                />
                <TextField
                    id="outlined-name"
                    label="Heizspule"
                    value={temp}
                    onChange={handleChange1}
                    variant="outlined"
                />
                <TextField
                    id="outlined-name"
                    label="Befeuchter"
                    value={hum}
                    onChange={handleChange2}
                    variant="outlined"
                />
                <button id="creat" onClick={sendData}>Create Account</button>
            </div>
        </form>
    );
}